

.Loading{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 80px;
}

.SectionTitle{
  max-width: 280px;
  /* background-color: #f9f9f9; */
  border-bottom: solid 3px #aaa;
  position: relative;
  margin: 80px auto 30px;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  padding: 12px;
}

.SectionTitle:not(:first-child){
  margin: 80px auto 30px;
}

.SectionTitle:after {
  position: absolute;
  content: " ";
  display: block;
  border-bottom: solid 3px grey;
  bottom: -3px;
  left: 0;
  width: 20%;
}

@media(min-width: 768px){
  .SectionTitle{
    margin: 0 auto 30px;
  }
}

.Leader{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 30px;
}


.LeaderImage{
  background-size: cover;
  background-position: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.DummyInvitation{
  width: 200px;
  height: 200px;
  background-color: #eee;
  margin: 0 auto 20px;
  -webkit-animation-name: loading;
          animation-name: loading;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.InvitationImage{
  margin-bottom: 10px;
  width: 200px;
  height: 200px;
  /* padding-right: 15px; */
}

@media(min-width: 768px){
  .DummyInvitation{
    width: 300px;
    height: 300px;
  }
  .InvitationImage{
    width: 300px;
    height: 300px;
  }
}



@-webkit-keyframes loading{
  0%   { background-color: #eee; }
  100% { background-color: #bbb; }
}



@keyframes loading{
  0%   { background-color: #eee; }
  100% { background-color: #bbb; }
}

.Invitation{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;

}



.InvitationURL{
  max-width: 300px;
  height: 40px;
  overflow: scroll;
}

.Users{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: start;
      align-items: start;
  max-width: 300px;
  margin: 0 auto;
}

.User{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 20px;
  margin-top: 0;
  width: 50%;
}

.UserImage{
  width: 80px;
  height: 80px;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  margin-bottom: 5px;
  background-color: #eee;
}

.Forward{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 50px 0;
}

@media(min-width: 768px){
  .User{
    width: 33%;
  }
}



